  margin-top: 30px; }

.mobile-menu li:first-child {
  margin-top: 0; }

.mobile-menu__submenu li:first-child {
  margin-top: 30px; }

.mobile-menu__cross {
  background-color: transparent;
  border: none;
  position: absolute;
  right: 27px;
  top: 17px;
  padding: 4px; }

.mobile-menu .cross {
  pointer-events: none;
  fill: #000000; }

.mobile-menu__trigger .arrow {
  transition: all 0.2s; }

.mobile-menu__trigger.open .arrow {
  transform: rotate(270deg); }

.mobile-menu__trigger button {
  border: none;
  background-color: transparent;
  font-size: 20px;
  color: #000000;
  padding: 0;
  position: static;
  display: flex;
  align-items: center;
  text-transform: uppercase; }

.mobile-menu__trigger:nth-child(2).open .mobile-menu__submenu {
  max-height: 276px; }

.mobile-menu__trigger:nth-child(3).open .mobile-menu__submenu {
  max-height: 414px; }

.mobile-menu__trigger.open .mobile-menu__submenu li a {
  display: inline-block;
  transform: translateX(-100%);
  opacity: 0;
  animation-fill-mode: forwards;
  animation-name: showLi;
  animation-timing-function: ease;
  animation-duration: 0.3s; }
  @media (max-width: 1024px) {
    .mobile-menu__trigger.open .mobile-menu__submenu li a {
      animation-duration: 0.15s; } }

.mobile-menu__submenu li:nth-child(1) a {
  animation-delay: 0s; }

.mobile-menu__submenu li:nth-child(2) a {
  animation-delay: 0.1s; }

.mobile-menu__submenu li:nth-child(3) a {
  animation-delay: 0.2s; }

.mobile-menu__submenu li:nth-child(4) a {
  animation-delay: 0.3s; }

.mobile-menu__submenu li:nth-child(5) a {
  animation-delay: 0.4s; }

.mobile-menu__submenu li:nth-child(6) a {
  animation-delay: 0.5s; }

.mobile-menu__submenu li:nth-child(7) a {
  animation-delay: 0.6s; }

.mobile-menu__submenu li:nth-child(8) a {
  animation-delay: 0.7s; }

.mobile-menu__submenu li:nth-child(9) a {
  animation-delay: 0.8s; }

.mobile-menu__submenu li:nth-child(10) a {
  animation-delay: 0.9s; }

.mobile-menu__submenu li:nth-child(11) a {
  animation-delay: 1s; }

.mobile-menu .arrow {
